<h1 id="yearning" tabindex="-1"><a class="header-anchor" href="#yearning" aria-hidden="true">#</a> Yearning</h1>
<p>Yearning 面向中小型企业的轻量级MySQL SQL语句审核平台.提供查询审计，SQL审核等多种功能.</p>
<p><a href="https://gitee.com/cookieYe/Yearning" target="_blank" rel="noopener noreferrer">Gitee<OutboundLink/></a>
<a href="https://github.com/cookieY/Yearning" target="_blank" rel="noopener noreferrer">GitHub<OutboundLink/></a></p>
<ol>
<li>
<p>SQL 查询</p>
<ul>
<li>查询工单</li>
<li>导出</li>
<li>自动补全，智能提示</li>
<li>查询语句审计</li>
<li>查询结果脱敏</li>
</ul>
</li>
<li>
<p>SQL 审核</p>
<ul>
<li>流程化工单</li>
<li>SQL语句语法检测</li>
<li>根据规则检测SQL语句合规性</li>
<li>自动生成DDL/DML回滚语句</li>
<li>历史审核记录</li>
</ul>
</li>
<li>
<p>推送</p>
<ul>
<li>E-mail 工单推送</li>
<li>钉钉 webhook 机器人工单推送</li>
</ul>
</li>
<li>
<p>用户权限及管理</p>
<ul>
<li>角色划分</li>
<li>基于用户的细粒度权限</li>
<li>注册</li>
</ul>
</li>
<li>
<p>其他</p>
<ul>
<li>todoList</li>
<li>LDAP 登录</li>
<li>动态审核规则配置</li>
<li>自定义审核层级</li>
</ul>
</li>
<li>
<p>AutoTask 自动执行</p>
</li>
</ol>
